      setInterval(function() {
        let tmpArr = [];
        option.series.forEach((item, index) => {
          tmpArr = [];
          for (let i = 0; i < option.yAxis.data.length; i++) {
            let num=option.series[index].data[i];
            tmpArr.push(num+_this.getRndInteger(1, 10));
          }
          option.series[index].data = tmpArr;
        });
        myChart.setOption(option, true);
      }, this.timer);
    },
    getRndInteger(min, max) {
      return Math.floor(Math.random() * (max - min)) + min;
    },
    getTop(lengedArr) {
      var legendData = [];
      var seriesData = [];
      var tmpArr = [];
      while (tmpArr.length < 10) {
        console.log(1);
        tmpArr.push({
          name: lengedArr[this.getRndInteger(0, 19)],
          value: this.getRndInteger(0, 1000000),
          textStyle: {
            color: "#fff"
          }
        });
        let uniqueArr = [...new Set(tmpArr)];
        tmpArr = uniqueArr;
      }
      seriesData = this.maopao(tmpArr);
      seriesData.forEach(element => {
        legendData.push(element.name);
      });
      return { legendData, seriesData };
